<template>
  <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span v-if="isNew"></span>
            <span v-else></span>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="title" class="form-label"></label>
                <input type="text" class="form-control" id="title" v-model="tempArticle.title" placeholder="" />
              </div>
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input type="text" class="form-control" id="image" v-model="tempArticle.imageUrl" placeholder="" />
              </div>
              <div class="mb-3">
                <label for="author" class="form-label"></label>
                <input type="text" class="form-control" id="author" v-model="tempArticle.author" placeholder="" />
              </div>
              <div class="mb-3">
                <label for="create_at"></label>
                <input type="date" class="form-control" id="create_at" v-model="create_at" />
              </div>
            </div>
            <div class="col-sm-8">
              <label for="tag" class="form-label"></label>
              <div class="row gx-1 mb-3">
                <div class="col-md-2 mb-1" v-for="(label, key) in tempArticle.tag" :key="key">
                  <div class="input-group input-group-sm">
                    <input type="text" class="form-control form-control" id="tag" v-model="tempArticle.tag[key]" placeholder="" />
                    <button type="button" class="btn btn-outline-danger" @click="tempArticle.tag.splice(key, 1)">
                      <i class="bi bi-x"></i>
                    </button>
                  </div>
                </div>
                <div class="col-md-2 mb-1" v-if="
                    tempArticle.tag[tempArticle.tag.length - 1] ||
                    !tempArticle.tag.length
                  ">
                  <button class="btn btn-outline-primary btn-sm d-block w-100" @click="tempArticle.tag.push('')">
                    
                  </button>
                </div>
              </div>
              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea type="text" class="form-control" id="description" v-model="tempArticle.description" placeholder=""></textarea>
              </div>
              <div class="mb-3">
                <ckeditor :editor="editor" :config="editorConfig" v-model="tempArticle.content"></ckeditor>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" v-model="tempArticle.isPublic" id="isPublic" />
                  <label class="form-check-label" for="isPublic">
                    
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
            
          </button>
          <button type="button" class="btn btn-primary" @click="$emit('update-article', tempArticle)">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import modalMixin from '@/mixins/modalMixin'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
export default {
  props: {
    article: {
      type: Object,
      default() {
        return {}
      }
    },
    isNew: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      status: {},
      modal: '',
      tempArticle: {
        tag: ['']
      },
      create_at: 0,
      // https://ckeditor.com/docs/ckeditor5/latest/builds/guides/integration/frameworks/vuejs-v3.html#editor
      editor: ClassicEditor,
      editorConfig: {
        toolbar: ['heading', 'typing', 'bold', 'italic', '|', 'link']
      }
    }
  },
  mixins: [modalMixin],
  watch: {
    article() {
      this.tempArticle = {
        ...this.article,
        tag: this.article.tag || [],
        isPublic: this.article.isPublic || false
      };
      [this.create_at] = new Date(this.tempArticle.create_at * 1000)
        .toISOString()
        .split('T')
    },
    create_at() {
      this.tempArticle.create_at = Math.floor(new Date(this.create_at) / 1000)
    }
  },
  methods: {
  }
}
</script>

<style>
.ck-editor__editable_inline {
  min-height: 300px;
}
</style>
